import React, { Component } from "react";
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      categories: [],
      jokes: {},
    };
  }

  componentDidMount() {
    // Getting the categories
    fetch("https://api.chucknorris.io/jokes/categories")
      .then((response) => response.json())
      .then((data) => this.setState({ categories: data }));
  }
  handleChange = (e) => {
    const cat = e.target.value;
    //Getting random joke from a slected category
    const url = `https://api.chucknorris.io/jokes/random?category=` + cat;
    fetch(url)
      .then((response) => response.json())
      .then((data) => this.setState({ jokes: data }));
  };
  render() {
    return (
      <div className="container">
        <h1 className="main-heading">Chuck Norris Jokes</h1>
        <select className="select-options" onChange={this.handleChange}>
          <option>Select By Category</option>
          {this.state.categories.map((category) => (
            <option key={category}>{category}</option>
          ))}
        </select>
        <div>
          <img src={this.state.jokes.icon_url} alt="Icon" />
          <h1 className="main-content">{this.state.jokes.value}</h1>
        </div>
      </div>
    );
  }
}
export default App;
